import appsync from '@/api/appsync'
import storage from '@/api/storage'
import aws_exports from '@/aws-exports'

const state = {
  guests: [],
  // steps: 1
  currentStep: 100
}

const mutations = {
  setGuests(state, val) {
    state.guests = val
  },
  setCurrentStep(state, val) {
    state.currentStep = val
  }
}

const actions = {
  async loadGuests ({ commit, rootState }, val) {
    const reservation = await appsync.retrieveReservation(val)

    // need to show error dialog here
    if (!reservation) {
      // need to show error dialog here
      console.error('no reservation')
      return
    }

    const guests = await appsync.retrieveGuests(val)

    if (guests.length < reservation.guestCount) {
      let i = guests.length

      for (i; i < reservation.guestCount; i++) {
        guests.push({ 
          reservationCode: val, 
          guestNo: i, 
          firstName: ' ', 
          lastName: ' ',
          imgBucket: aws_exports.aws_user_files_s3_bucket,
          idImgName: `id/${i}.jpg`,
          idImgKey: `private/${rootState.amplify.userIdentityId}/id/${i}.jpg`,
          faceImgName: `face/${i}.jpg`,
          faceImgKey: `private/${rootState.amplify.userIdentityId}/face/${i}.jpg`,
          lastUpdateOn: null
        })
      }
    }

    commit('setGuests', guests)
    commit('setCurrentStep', 1)
    // commit('setSteps', guests.length)

  },
  async saveGuest ({ commit }, guest) {
    if (guest.lastUpdateOn) {
      guest.lastUpdateOn = Math.round((new Date).getTime() / 1000)
      const reservation = await appsync.updateReservationGuest(guest)
      // console.log(reservation)
    } else {
      guest.lastUpdateOn = Math.round((new Date).getTime() / 1000)
      const reservation = await appsync.createReservationGuest(guest)
      // console.log(reservation)
    }
  },
  async putImage ({ commit }, {guest, isId}) {
    if (isId) {
      await storage.put(guest.idImgName, guest.idBase64Img.split(',')[1])
    } else {
      await storage.put(guest.faceImgName, guest.faceBase64Img.split(',')[1])
    }
  },
  setCurrentStep ({ commit }, val) {
    commit('setCurrentStep', val)
  }
}

const getters = {
    steps: state => {
      return state.guests.length
    },
    guest: state => {
      return state.guests[state.currentStep - 1]
    },
    currentStep: state => {
      return state.currentStep
    },
    idImgSrc: state => {

    },
    faceImgSrc: state => {
    }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}